.exercise-template {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  min-height: 90vh;
}

.math-exercise,
.code-snippet {
  -ms-flex: 1 1 350px;
      flex: 1 1 350px;
  padding: 1rem;
}

.code-snippet iframe {
  width: 100%;
  min-height: 300px;
  overflow-y: scroll;
}

input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

.home-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 1rem 4rem;
  -ms-flex-pack: center;
      justify-content: center;
}

.home-item {
  -ms-flex: 1 2;
      flex: 1 2;
  min-width: 400px;
  height: 200px;
  padding: 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



